<template>
    <RouterView />
</template>
<script>
import socketIO from "@/socket";

export default {
    created() {
        this.socket = socketIO;
        this.socket.on("member_disconnected", () => {
            this.socket.emit("abbandon_game");
            this.$router.replace({ name: "start" });
        });
        document.title = "Number Finder";
    },
    data() {
        return {
            socket: null,
        };
    },
    methods: {
        abbandonToast() {
            const oldToast = document
                .getElementById("app")
                .querySelector(".toast");
            if (oldToast) oldToast.remove();

            const toast = document.createElement("div");

            toast.classList.add("toast");

            toast.innerHTML = `
				<span class="material-symbols-rounded toast_icon">person_remove</span>
				<h3 class="toast_text">The Other Player Has Abbandoned</h3>
				<div class="toast_timer"></div>
			`;

            document.getElementById("app").appendChild(toast);
            toast.style.left = `${
                document.getElementById("app").offsetWidth / 2 -
                toast.offsetWidth / 2
            }px`;
            setTimeout(() => {
                toast.remove();
            }, 1500);
        },
    },
};
</script>
